/*desktop devices*/

@media all and (min-width: 901px) {
	.app-wrapper {
		grid-template-areas: 
		"header  header  	header   header" 
		".       navbar  	cont          ." 
		".       navfr   	cont          ." 
		".       navmusic   cont          ." 
		"footer  footer  	footer   footer";
		grid-template-rows: auto 284px 232px auto 50px;
		grid-template-columns: 2fr 1.3fr 5fr 2fr;
		grid-gap: 10px;
	}
}

/* START mobile devices */

/* < 575px */

@media all and (max-width: 575px) {
	.app-wrapper {
		grid-template-areas: 
		"header header" 
		"navbar navfr" 
		"cont cont"
		"navmusic navmusic" 
		"footer footer";

		grid-template-rows: 60px auto auto auto 60px;
		grid-template-columns: auto auto;
		grid-gap: 10px;
	}
}

/* < 900px */

@media all and (max-width: 900px) {
	.app-wrapper {
		grid-template-areas: 
		"header header" 
		"navbar navfr"
		"cont cont" 
		"navmusic navmusic"
		"footer footer";

		grid-template-rows: 60px auto auto auto 60px;
		grid-template-columns: auto auto;
		grid-gap: 10px;
	}
}

/* END mobile devices */

